#dashboard {
	display: flex;
	flex-direction: column;
	height: 100vh;
	display: none;
}
header {
	background-color: #007bff;
	color: white;
	padding: 10px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
h1 {
	color: gold;
}
button {
	width: 100px;
	height: 25px;
	background-color: red;
	color: #ffff;
	font-weight: bold;
}
#unLoggedDash {
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	height: 100vh;
}
h1 {
	text-align: center;
}
button {
	width: 100px;
	height: 25px;
	background-color: greenyellow;
	color: #333;
	font-weight: bold;
}
input {
	height: 25px;
}
